import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from '../admin.service';

@Component({
  selector: 'app-purchase',
  templateUrl: './purchase.component.html',
  styleUrls: ['./purchase.component.css']
})
export class PurchaseComponent implements OnInit {
data;
  constructor(private service:AdminService,private router:Router,private activatedRoute:ActivatedRoute) { }

  ngOnInit(): void {
  }

  addPurchase(myForm)
  {
    console.log(myForm.form.value) ;
    this.data=myForm.form.value;
    this.service.addnewPurchase(this.data).subscribe((res)=>{
      this.router.navigate(['../purchaselist'], {relativeTo: this.activatedRoute});
      })
  }

}
