import { Component, OnInit } from '@angular/core';
import {jsPDF} from 'jspdf';
import html2Canvas from 'html2Canvas'

@Component({
  selector: 'app-estpdf',
  templateUrl: './estpdf.component.html',
  styleUrls: ['./estpdf.component.css']
})
export class EstpdfComponent implements OnInit {
data;
data1;
estimateId;
amount;
expiry:Date;
cgst;
sgst;
halfgst;
  constructor() { }

  ngOnInit(): void {
    this.data=JSON.parse(sessionStorage['myForm'])
    this.data1=JSON.parse(sessionStorage['estimate'])
    console.log(this.data1+"response")
    this.estimateId=this.data1.estimateId
    this.amount=this.data.amount
    // this.data.date.setDate(this.data1.date + 3)
    this.cgst=this.data1.price * this.data1.qty * 0.5 * this.data.gst/100;
    this.sgst=this.cgst;
    this.halfgst=0.5 * this.data1.gst
    
  }

  generatePdf(){
    var data1=document.getElementById('body');
    
     // document.getElementById("cgst").style.display = "none";    					 
    
    html2Canvas(data1).then(canvas=>{
      var imgWidth=208;
      var pageHeight=295;
      var imgHeight=canvas.height*imgWidth/canvas.width;
      var heightLeft=imgHeight;

      const contentDataURL=canvas.toDataURL('image/png')
      let pdf=new jsPDF('p','mm','a4');
      var position=0;
      pdf.addImage(contentDataURL,'PNG',0,position,imgWidth,imgHeight)
      pdf.save('Estimate'+ this.data1.estimateId +'.pdf');
    });

  }

}
