import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from '../admin.service';

@Component({
  selector: 'app-adblue-edit',
  templateUrl: './adblue-edit.component.html',
  styleUrls: ['./adblue-edit.component.css']
})
export class AdblueEditComponent implements OnInit {
  data;
  constructor(private service:AdminService,private router:Router,private activatedRoute:ActivatedRoute) { }
  
    ngOnInit(): void {
      this.data=JSON.parse(sessionStorage['editAdblue'])
    }
  
    update(myForm){
      console.log(myForm+'myForm')
      this.service.updateAdblue(myForm.form.value).subscribe((res)=>{
        console.log(res)
        if(res!=null)
        alert("saved")
        this.router.navigate(['../addblueList'], {relativeTo: this.activatedRoute});
            })
    }

}
