import { Component, OnInit } from '@angular/core';
import {jsPDF} from 'jspdf';
import html2Canvas from 'html2Canvas'
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from '../admin.service';

@Component({
  selector: 'app-invoicepdf',
  templateUrl: './invoicepdf.component.html',
  styleUrls: ['./invoicepdf.component.css']
})
export class InvoicepdfComponent implements OnInit {
  data;
  productData;
  data1;
  invoiceId;
  amount;
  expiry:Date;
  cgst;
  sgst;
  halfgst;
  total;
  constructor(private router:Router,private activatedRoute:ActivatedRoute,private service:AdminService) { }

  ngOnInit(): void {
    // this.data=JSON.parse(sessionStorage['myFormInvoice'])
    this.data1=JSON.parse(sessionStorage['invoice'])
    console.log(this.data1+"response")
    this.invoiceId=this.data1.invoiceId
    this.cgst=this.data1.price * this.data1.qty * 0.5 * this.data1.gst/100;
    this.sgst=this.cgst;
    this.halfgst=0.5 * this.data1.gst
    this.service.prodList(this.data1).subscribe((res)=>{
      console.log(res+"mast")
this.productData=res;
    })
    for (let i = 0; i < 3; i++) {
      this.total=this.productData.amount+this.total
    }
    console.log(this.total+"total")
    this.amount=this.data1.amount
    // this.data.date.setDate(this.data1.date + 3)
    
  }

  generatePdf(){
    var data1=document.getElementById('body');
    html2Canvas(data1).then(canvas=>{
      var imgWidth=208;
      var pageHeight=295;
      var imgHeight=canvas.height*imgWidth/canvas.width;
      var heightLeft=imgHeight;

      const contentDataURL=canvas.toDataURL('image/png')
      let pdf=new jsPDF('p','mm','a4');
      var position=0;
      pdf.addImage(contentDataURL,'PNG',0,position,imgWidth,imgHeight)
      pdf.save('Invoice'+ this.data1.invoiceId +'.pdf');
    });

  }
  addProduct(data1){
    sessionStorage['invoiceID']=JSON.stringify(data1)
    this.router.navigate(['../product'], {relativeTo: this.activatedRoute});
  }

}
