import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from '../admin.service';

@Component({
  selector: 'app-attach-transporter-edit',
  templateUrl: './attach-transporter-edit.component.html',
  styleUrls: ['./attach-transporter-edit.component.css']
})
export class AttachTransporterEditComponent implements OnInit {

  data;
  constructor(private service:AdminService,private router:Router,private activatedRoute:ActivatedRoute) { }
  
    ngOnInit(): void {
      this.data=JSON.parse(sessionStorage['editAttachTransporter'])
    }
  
    update(myForm){
      console.log(myForm+'myForm')
      this.service.updateAttachTransporter(myForm.form.value).subscribe((res)=>{
        console.log(res)
        if(res!=null)
        alert("saved")
        this.router.navigate(['../attachTransporterList'], {relativeTo: this.activatedRoute});
            })
    }

}
