import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from '../admin.service';

@Component({
  selector: 'app-invoice',
  templateUrl: './invoice.component.html',
  styleUrls: ['./invoice.component.css']
})
export class InvoiceComponent implements OnInit {
data;
  constructor(private service:AdminService,private router:Router,private activatedRoute:ActivatedRoute) { }

  ngOnInit(): void {
    document.getElementById("welcome").innerText="";
  }
add(myForm){
  console.log(myForm.form.value) ;
  this.data=myForm.form.value;
 sessionStorage['myFormInvoice']=JSON.stringify(this.data);
 

 this.service.addnewinvoice(this.data).subscribe((res)=>{
  alert("Invoice Saved Successfully");
  console.log(res);
  
  sessionStorage['invoice']=JSON.stringify(res)
   this.router.navigate(['../invoicepdf'], {relativeTo: this.activatedRoute});
 })
}
}
