import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from '../admin.service';

@Component({
  selector: 'app-attach-transporter',
  templateUrl: './attach-transporter.component.html',
  styleUrls: ['./attach-transporter.component.css']
})
export class AttachTransporterComponent implements OnInit {

  constructor(private service:AdminService,private router:Router,private activatedRoute:ActivatedRoute) { }

  ngOnInit(): void {
  }

  add(myForm)
  {
    console.log("in add")
    this.service.addAttachTransporter(myForm.form.value).subscribe((res)=>{
console.log(res)
if(res!=null)
alert("saved")
this.router.navigate(['../attachTransporterList'], {relativeTo: this.activatedRoute});
    })
  }

}
