import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from '../admin.service';

@Component({
  selector: 'app-trip-list',
  templateUrl: './trip-list.component.html',
  styleUrls: ['./trip-list.component.css']
})
export class TripListComponent implements OnInit {
  public cancelClicked:boolean=false;
  public popoverTitle:"delete"
  public popoverMessage="Do you really want to delete?"
  data;
  constructor(private service:AdminService,private router:Router,private activatedRoute:ActivatedRoute) { }

  ngOnInit(): void {
    document.getElementById("welcome").innerText="";
    this.service.getAllTripDetails().subscribe((res)=>{
      console.log(res);
      this.data=res;
    })
  }
  delete(id)
  {
    this.service.deleteTrip(id).subscribe((res)=>{
      if(res!=null)
      this.ngOnInit()
    })
  }

  edit(data1)
  {
    console.log(data1.id+'data1')
    sessionStorage['editTrip']=JSON.stringify(data1)
    this.router.navigate(['../tripEdit'], {relativeTo: this.activatedRoute});
  }

}
