import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from '../admin.service';

@Component({
  selector: 'app-driver-edit',
  templateUrl: './driver-edit.component.html',
  styleUrls: ['./driver-edit.component.css']
})
export class DriverEditComponent implements OnInit {

  data;
  constructor(private service:AdminService,private router:Router,private activatedRoute:ActivatedRoute) { }
  
    ngOnInit(): void {
      this.data=JSON.parse(sessionStorage['editDriver'])
    }
  
    update(myForm){
      console.log(myForm+'myForm')
      this.service.updateDriver(myForm.form.value).subscribe((res)=>{
        console.log(res)
        if(res!=null)
        alert("saved")
        this.router.navigate(['../driverList'], {relativeTo: this.activatedRoute});
            })
    }

}
