import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from '../admin.service';

@Component({
  selector: 'app-customer-edit',
  templateUrl: './customer-edit.component.html',
  styleUrls: ['./customer-edit.component.css']
})
export class CustomerEditComponent implements OnInit {
data;
constructor(private service:AdminService,private router:Router,private activatedRoute:ActivatedRoute) { }

  ngOnInit(): void {
    this.data=JSON.parse(sessionStorage['editCustomer'])
  }

  update(myForm){
    console.log(myForm+'myForm')
    this.service.updateCustomer(myForm.form.value).subscribe((res)=>{
      console.log(res)
      if(res!=null)
      alert("saved")
      this.router.navigate(['../customerList'], {relativeTo: this.activatedRoute});
          })
  }

}
