import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from '../admin.service';

@Component({
  selector: 'app-tanker-edit',
  templateUrl: './tanker-edit.component.html',
  styleUrls: ['./tanker-edit.component.css']
})
export class TankerEditComponent implements OnInit {
data;
  constructor(private service:AdminService,private router:Router,private activatedRoute:ActivatedRoute) { }

  ngOnInit(): void {
    this.data=JSON.parse(sessionStorage['editTanker'])
  }

  update(myForm){
    console.log(myForm+'myForm')
    this.service.updateCustomer(myForm.form.value).subscribe((res)=>{
      console.log(res)
      if(res!=null)
      alert("saved")
      this.router.navigate(['../tankerList'], {relativeTo: this.activatedRoute});
          })
  }

}
