import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AdminService } from '../admin.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-new',
  templateUrl: './new.component.html',
  styleUrls: ['./new.component.css']
})
export class NewComponent implements OnInit {
  data;
  constructor(private service:AdminService,private router:Router,private activatedRoute:ActivatedRoute) { }

  ngOnInit(): void {
    document.getElementById("welcome").innerText="";
  }

  add(myForm)
{
 console.log(myForm.form.value) ;
this.data=myForm.form.value;
this.service.addnewinstallation(this.data).subscribe((res)=>{
  this.router.navigate(['../installation'], {relativeTo: this.activatedRoute});
})
}
}
