import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from '../admin.service';

@Component({
  selector: 'app-invoicelist',
  templateUrl: './invoicelist.component.html',
  styleUrls: ['./invoicelist.component.css']
})
export class InvoicelistComponent implements OnInit {
data;
  constructor(private service:AdminService,private router:Router,private activatedRoute:ActivatedRoute) { }

  ngOnInit(): void {
    document.getElementById("welcome").innerText="";
    this.service.getAllInvoice().subscribe((res)=>{
      console.log(res);
      this.data=res;
    })
  }
  delete(invoiceId)
  {
    this.service.deleteInvoice(invoiceId).subscribe((res)=>{
      if(res==1)
      this.ngOnInit()
    })
  }

  openInvoice(d)
  {
    console.log(d)
    sessionStorage['invoice']=JSON.stringify(d)
    this.router.navigate(['../invoicepdf'], {relativeTo: this.activatedRoute});
  }

}
