import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from '../admin.service';

@Component({
  selector: 'app-firm-list-edit',
  templateUrl: './firm-list-edit.component.html',
  styleUrls: ['./firm-list-edit.component.css']
})
export class FirmListEditComponent implements OnInit {
data;
  constructor(private service:AdminService,private router:Router,private activatedRoute:ActivatedRoute) { }

  ngOnInit(): void {
    this.data=JSON.parse(sessionStorage['editFirm'])
    console.log(this.data+'---')
  }

  update(myForm){
    console.log(myForm+'myForm')
    this.service.updateFirm(myForm.form.value).subscribe((res)=>{
      console.log(res)
      if(res!=null)
      alert("saved")
      this.router.navigate(['../firmList'], {relativeTo: this.activatedRoute});
          })
  }

}
