import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from '../admin.service';

@Component({
  selector: 'app-tyre-details',
  templateUrl: './tyre-details.component.html',
  styleUrls: ['./tyre-details.component.css']
})
export class TyreDetailsComponent implements OnInit {
  public cancelClicked:boolean=false;
  public popoverTitle:"delete"
  public popoverMessage="Do you really want to delete?"
  data;
  location;
  constructor(private service:AdminService,private router:Router,private activatedRoute:ActivatedRoute) { }

  ngOnInit(): void {
    document.getElementById("welcome").innerText="";
    this.location=JSON.parse(sessionStorage['location']);
console.log(this.location)
if(this.location!=null){
    if(this.location.name=='Wai')
    {
      sessionStorage.setItem('location',JSON.stringify(null));
      this.service.getWaiTyreDetails().subscribe((res)=>{
        console.log(res);
        this.data=res;
      })
    }else if(this.location.name=='Pachgani')
    {
     // sessionStorage.removeItem("location");
      sessionStorage.setItem('location',JSON.stringify(null));
      this.service.getPachganiTyreDetails().subscribe((res)=>{
        console.log(res);
        this.data=res;
      })
    }else if(this.location.name=='Chaufula')
    {
      sessionStorage.setItem('location',JSON.stringify(null));
      this.service.getChaufulaTyreDetails().subscribe((res)=>{
        console.log(res);
        this.data=res;
      })
    }
  }
    else{
      this.service.getAllTyreDetails().subscribe((res)=>{
        console.log(res);
        this.data=res;
      })
    }
 
  }
  delete(id)
  {
    this.service.deleteTyre(id).subscribe((res)=>{
      if(res!=null)
      this.ngOnInit()
    })
  }

  edit(data1)
  {
    console.log(data1.id+'data1')
    sessionStorage['editTrip']=JSON.stringify(data1)
    this.router.navigate(['../tripEdit'], {relativeTo: this.activatedRoute});
  }

}
