import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from '../admin.service';

@Component({
  selector: 'app-tanker',
  templateUrl: './tanker.component.html',
  styleUrls: ['./tanker.component.css']
})
export class TankerComponent implements OnInit {
sixTyre=false;
tenTyre=false;
twelveTyre=false;
fourteenTyre=false;
sixteenTyre=false;
oneStepney=false;
twoStepney=false;
threeStepney=false;
fourStepney=false;
  constructor(private service:AdminService,private router:Router,private activatedRoute:ActivatedRoute) { }

  ngOnInit(): void {
  }

  tyreBlock(tyreCount){
    console.log(tyreCount)
    if(tyreCount==6)
    this.sixTyre=true;

    else if(tyreCount==10){
      this.sixTyre=false;
      this.twelveTyre=false;
      this.tenTyre=true;
      this.sixteenTyre=false;
    }

    else if(tyreCount==12){
      this.sixTyre=false;
      this.tenTyre=false;
      this.twelveTyre=true;
      this.fourteenTyre=false;
      this.sixteenTyre=false;
    }
    
    else if(tyreCount==14){
      this.tenTyre=false;
      this.sixTyre=false;
      this.twelveTyre=true;
      this.fourteenTyre=true;
      this.sixteenTyre=false;
    }else
    {
      this.tenTyre=false;
      this.sixTyre=false;
      this.twelveTyre=true;
      this.fourteenTyre=false;
      this.sixteenTyre=true;
    }
   
  }

  stepneyBlock(stepney_count){
    console.log(stepney_count)
    if(stepney_count==1)
    this.oneStepney=true;
    else if(stepney_count==2){
      this.twoStepney=true;
      this.threeStepney=false;
      this.fourStepney=false;
      this.oneStepney=false;
    }
    else if(stepney_count==3){
      this.threeStepney=true;
      this.twoStepney=false;
      this.fourStepney=false;
      this.oneStepney=false;
    }else{
      this.fourStepney=true;
      this.oneStepney=false;
      this.twoStepney=false;
      this.threeStepney=false;
    }
  }

  add(myForm)
  {
    console.log("in add")
    this.service.addTanker(myForm.form.value).subscribe((res)=>{
console.log(res)
if(res!=null)
alert("saved")
this.router.navigate(['../tankerList'], {relativeTo: this.activatedRoute});
    })
  }
}
