import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from '../admin.service';

@Component({
  selector: 'app-tyre-add',
  templateUrl: './tyre-add.component.html',
  styleUrls: ['./tyre-add.component.css']
})
export class TyreAddComponent implements OnInit {


  constructor(private service:AdminService,private router:Router,private activatedRoute:ActivatedRoute) { }

  ngOnInit(): void {
  }

  add(myForm)
  {
    console.log("in add")
    this.service.addTyre(myForm.form.value).subscribe((res)=>{
console.log(res)
sessionStorage['location']=JSON.stringify("all")
if(res!=null)
alert("saved")
this.router.navigate(['../tyreDetails'], {relativeTo: this.activatedRoute});
    })
  }
}
