import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from '../admin.service';

@Component({
  selector: 'app-credit-add',
  templateUrl: './credit-add.component.html',
  styleUrls: ['./credit-add.component.css']
})
export class CreditAddComponent implements OnInit {


  constructor(private service:AdminService,private router:Router,private activatedRoute:ActivatedRoute) { }

  ngOnInit(): void {
  }

  add(myForm)
  {
    console.log("in add")
    this.service.addCredit(myForm.form.value).subscribe((res)=>{
console.log(res)
if(res!=null)
alert("saved")
this.router.navigate(['../creditList'], {relativeTo: this.activatedRoute});
    })
  }

}
