import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from '../admin.service';

@Component({
  selector: 'app-party-list',
  templateUrl: './party-list.component.html',
  styleUrls: ['./party-list.component.css']
})
export class PartyListComponent implements OnInit {
  public cancelClicked:boolean=false;
  public popoverTitle:"delete"
  public popoverMessage="Do you really want to delete?"
  data;
  constructor(private service:AdminService,private router:Router,private activatedRoute:ActivatedRoute) { }

  ngOnInit(): void {
    document.getElementById("welcome").innerText="";
    this.service.getAllPartyDetails().subscribe((res)=>{
      console.log(res);
      this.data=res;
    })
  }
  delete(id)
  {
    this.service.deleteParty(id).subscribe((res)=>{
      if(res!=null)
      this.ngOnInit()
    })
  }
  edit(data1)
  {
    sessionStorage['editParty']=JSON.stringify(data1)
    this.router.navigate(['../partyEdit'], {relativeTo: this.activatedRoute});
  }

}
