import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from '../admin.service';

@Component({
  selector: 'app-firm-list1',
  templateUrl: './firm-list1.component.html',
  styleUrls: ['./firm-list1.component.css']
})
export class FirmList1Component implements OnInit {
  public cancelClicked:boolean=false;
  public popoverTitle:"delete"
  public popoverMessage="Do you really want to delete?"
  data;
  constructor(private service:AdminService,private router:Router,private activatedRoute:ActivatedRoute) { }

  ngOnInit(): void {
    document.getElementById("welcome").innerText="";
    this.service.getAllFirmDetails().subscribe((res)=>{
      console.log(res);
      this.data=res;
    })
  }
  delete(id)
  {
    this.service.deleteFirm(id).subscribe((res)=>{
      if(res!=null)
      this.ngOnInit()
    })
  }

  
  edit(data1)
  {
    console.log(data1.id+'data1')
    sessionStorage['editFirm']=JSON.stringify(data1)
    this.router.navigate(['../firmEdit'], {relativeTo: this.activatedRoute});
  }

}
