import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from '../admin.service';

@Component({
  selector: 'app-supplier-edit',
  templateUrl: './supplier-edit.component.html',
  styleUrls: ['./supplier-edit.component.css']
})
export class SupplierEditComponent implements OnInit {

  data;
  constructor(private service:AdminService,private router:Router,private activatedRoute:ActivatedRoute) { }

  ngOnInit(): void {
    this.data=JSON.parse(sessionStorage['editSupplier'])
  }

  update(myForm){
    console.log(myForm+'myForm')
    this.service.updateSupplier(myForm.form.value).subscribe((res)=>{
      console.log(res)
      if(res!=null)
      alert("saved")
      this.router.navigate(['../supplierList'], {relativeTo: this.activatedRoute});
          })
  }

}
