import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from '../admin.service';

@Component({
  selector: 'app-addblue-list',
  templateUrl: './addblue-list.component.html',
  styleUrls: ['./addblue-list.component.css']
})
export class AddblueListComponent implements OnInit {
  public cancelClicked:boolean=false;
  public popoverTitle:"delete"
  public popoverMessage="Do you really want to delete?"
  data;
  constructor(private service:AdminService,private router:Router,private activatedRoute:ActivatedRoute) { }

  ngOnInit(): void {
    document.getElementById("welcome").innerText="";
    this.service.getAlladdbluesupplier().subscribe((res)=>{
      console.log(res);
      this.data=res;
    })
  }
  delete(id)
  {
    this.service.deleteSupplier(id).subscribe((res)=>{
      if(res!=null)
      this.ngOnInit()
    })
  }
  edit(data1)
  {
    console.log(data1.id+'data1')
    sessionStorage['editAdblue']=JSON.stringify(data1)
    this.router.navigate(['../adblueEdit'], {relativeTo: this.activatedRoute});
  }

}
