import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from '../admin.service';

@Component({
  selector: 'app-customer',
  templateUrl: './customer.component.html',
  styleUrls: ['./customer.component.css']
})
export class CustomerComponent implements OnInit {

  constructor(private service:AdminService,private router:Router,private activatedRoute:ActivatedRoute) { }

  ngOnInit(): void {
  }

  add(myForm)
  {
    console.log("in add")
    this.service.addCustomer(myForm.form.value).subscribe((res)=>{
console.log(res)
if(res!=null)
alert("saved")
this.router.navigate(['../customerList'], {relativeTo: this.activatedRoute});
    })
  }
}
