import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from '../admin.service';

@Component({
  selector: 'app-addblue',
  templateUrl: './addblue.component.html',
  styleUrls: ['./addblue.component.css']
})
export class AddblueComponent implements OnInit {

  constructor(private service:AdminService,private router:Router,private activatedRoute:ActivatedRoute) { }

  ngOnInit(): void {
  }

  add(myForm)
  {
    console.log("in add")
    this.service.addblue(myForm.form.value).subscribe((res)=>{
console.log(res)
if(res!=null)
alert("saved")
this.router.navigate(['../addblueList'], {relativeTo: this.activatedRoute});
    })
  }

}
