import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from '../admin.service';
// import * as CanvasJS from '@angular/canvasjs.min';

@Component({
  selector: 'app-tyre-storage',
  templateUrl: './tyre-storage.component.html',
  styleUrls: ['./tyre-storage.component.css']
})



export class TyreStorageComponent implements OnInit {


  constructor(private service:AdminService,private router:Router,private activatedRoute:ActivatedRoute) { }
  dataTyreChaufula;
  dataTyrePachgani;
  dataTyreWai;
  surveyData;
  ngOnInit(): void {
    document.getElementById("welcome").innerText="";
    this.service.getAllTyreLocationDetails().subscribe((res)=>{
      console.log(res);
      this.dataTyreChaufula=res[0].count_c;
      this.dataTyrePachgani=res[1].count_c;
      this.dataTyreWai=res[2].count_c;

      this.surveyData = [
        { name: 'Chaufula', value: res[0].count_c },
        { name: 'Pachgani', value: res[1].count_c },
        { name: 'Wai', value: res[2].count_c },
        
      ];
    //  this.dataTyre=res;
    })
  }

  colorScheme = {
    domain: [
      '#94037c', 
      '#0295c2',
      '#bf0247', 
      '#80D8FF', 
      '#A7FFEB', 
      '#CCFF90', 
      '#FFFF8D', 
      '#FF9E80'
    ]

  };

  onSelect(event) {
    console.log(event)
    sessionStorage['location']=JSON.stringify(event)
    this.router.navigate(['../tyreDetails'], {relativeTo: this.activatedRoute});
  }
}
