import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from '../admin.service';

@Component({
  selector: 'app-debit-add',
  templateUrl: './debit-add.component.html',
  styleUrls: ['./debit-add.component.css']
})
export class DebitAddComponent implements OnInit {

expense;
  constructor(private service:AdminService,private router:Router,private activatedRoute:ActivatedRoute) { }

  ngOnInit(): void {
    this.service.getAllTankerExpenseDetails().subscribe((res)=>{
      console.log(res)
      this.expense=res;
    })
  }

  add(myForm)
  {
    console.log("in add")
    this.service.addDebit(myForm.form.value).subscribe((res)=>{
console.log(res)
if(res!=null)
alert("saved")
this.router.navigate(['../debitList'], {relativeTo: this.activatedRoute});
    })
  }

}
