import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from '../admin.service';

@Component({
  selector: 'app-tanker-expense-list',
  templateUrl: './tanker-expense-list.component.html',
  styleUrls: ['./tanker-expense-list.component.css']
})
export class TankerExpenseListComponent implements OnInit {

  data;
  constructor(private service:AdminService,private router:Router,private activatedRoute:ActivatedRoute) { }

  ngOnInit(): void {
    document.getElementById("welcome").innerText="";
    this.service.getAllTankerExpenseDetails().subscribe((res)=>{
      console.log(res);
      this.data=res;
    })
  }
  delete(id)
  {
    this.service.deleteTankerExpense(id).subscribe((res)=>{
      if(res!=null)
      this.ngOnInit()
    })
  }



  edit(data1)
  {
    sessionStorage['editSupplier']=JSON.stringify(data1)
    this.router.navigate(['../supplierEdit'], {relativeTo: this.activatedRoute});
  }

}
