import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from '../admin.service';

@Component({
  selector: 'app-trip-details',
  templateUrl: './trip-details.component.html',
  styleUrls: ['./trip-details.component.css']
})
export class TripDetailsComponent implements OnInit {

  constructor(private service:AdminService,private router:Router,private activatedRoute:ActivatedRoute) { }

  ngOnInit(): void {
    document.getElementById("welcome").innerText="";
  }

  add(myForm)
  {
    console.log("in add")
    this.service.addTripDetails(myForm.form.value).subscribe((res)=>{
console.log(res)
if(res!=null)
alert("saved")
this.router.navigate(['../tankerList'], {relativeTo: this.activatedRoute});
    })
  }
}
