import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from '../admin.service';

@Component({
  selector: 'app-lr-list',
  templateUrl: './lr-list.component.html',
  styleUrls: ['./lr-list.component.css']
})
export class LrListComponent implements OnInit {
  public cancelClicked:boolean=false;
  public popoverTitle:"delete"
  public popoverMessage="Do you really want to delete?"
    data;
    constructor(private service:AdminService,private router:Router,private activatedRoute:ActivatedRoute) { }
  
    ngOnInit(): void {
      document.getElementById("welcome").innerText="";
      this.service.getAllLRDetails().subscribe((res)=>{
        console.log(res);
        this.data=res;
      })
    }
  
    
    delete(id)
    {
  
      this.service.deleteLr(id).subscribe((res)=>{
        if(res!=null)
        this.ngOnInit()
      })
    }
  
    edit(data1)
    {
      sessionStorage['editMechanic']=JSON.stringify(data1)
      this.router.navigate(['../mechanicEdit'], {relativeTo: this.activatedRoute});
    }
  
  }
