import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdminService } from '../admin.service';

@Component({
  selector: 'app-attach-transporter-list',
  templateUrl: './attach-transporter-list.component.html',
  styleUrls: ['./attach-transporter-list.component.css']
})
export class AttachTransporterListComponent implements OnInit {
  public cancelClicked:boolean=false;
  public popoverTitle:"delete"
  public popoverMessage="Do you really want to delete?"
  data;
  constructor(private service:AdminService,private router:Router,private activatedRoute:ActivatedRoute) { }

  ngOnInit(): void {
    document.getElementById("welcome").innerText="";
    this.service.getAllAttachTransporterDetails().subscribe((res)=>{
      console.log(res);
      this.data=res;
    })
  }
  delete(id)
  {
console.log(id)
    this.service.deleteAttachTransporter(id).subscribe((res)=>{
      if(res!=null)
      this.ngOnInit()
    })
  }

  edit(data1)
  {
    console.log(data1.id+'data1')
    sessionStorage['editAttachTransporter']=JSON.stringify(data1)
    this.router.navigate(['../attachTransporterEdit'], {relativeTo: this.activatedRoute});
  }

}
